.footer{width:100%;margin-top:40px;height:120px;padding-top:7px}.footer,.footerContainer{background-color:#ededed}.footer a:link,.footer li a:visited{color:#2d5a96}.footerLinks{list-style-type:none;list-style-image:none;padding-left:0;padding-top:10px;float:left;margin-bottom:0}.footerLinks ul{list-style-type:none;margin:0;padding:0;overflow:hidden;float:right;margin-bottom:10px!important}.footerLinks li{float:left;padding-right:5px;font-size:12px;margin-left:0}.footerLinks li a:link,.footerLinks li a:visited{color:#2d5a96;padding-right:4px;padding-left:4px;font-weight:600}.footerLinks li:not(:first-child):before{content:" | ";color:#2d5a96}.nspiresLogo{background:url(/img/nspiresFooter.png) no-repeat transparent;float:right;width:104px;height:31px;margin-top:20px}.nasaFooterLogo{float:left;margin-right:5px;margin-top:8px}.nasaFooterLogo span{display:inline-block;width:55px;height:45px;background:url(/img/nasa-logo.svg) no-repeat transparent;background-size:auto auto;background-size:55px 45px}.footerInfo{padding-top:13px;list-style:none;float:left;width:auto;padding-left:0;margin-bottom:10px!important}.footerInfo li{margin-bottom:3px;color:#333;font-weight:600;font-size:12px}.footerContent{border-bottom:1px solid #d6d6d6}